import {mapState,mapMutations, mapActions, mapGetters} from 'vuex'
import $ from 'jquery'
export default {
  props: {
    layout: {
      type: String,
      default: "card",
    }
  },
  data() {
    return {
      jobcatas: ["產業別","政府機關、公部門","非營利組織相關","教育業","學生","自由接案","大眾傳播、出版相關","設計與文創相關","藝術文化相關","流行與時尚文化相關","旅遊、休閒、運動產業","金融投顧、保險相關","法律相關","一般服務業","一般傳統製造","運輸物流、倉儲、貿易","農、林、漁、牧業","礦業土石、水電能源","建築營造、不動產相關","醫療照護、環境衛生","批發零售","電子科技、資訊、軟體、半導體","其他"],
      registerData: {
        email: "",
        name: "",
        password: "",
        jobcata: "",
        job: "",
        
      },
      loginData: {
        email:  localStorage.zashare_auth_user_email || "",
        password: "",
        
      },
      mode: "login",
    }
  },
  computed: {
    ...mapState(['menuState','auth']),
    jobInforLabel(){
      if (this.registerData.jobcata=="學生"){
        return "學校系所"
      }
      return "職稱"
    }
  },
  methods: {
    ...mapMutations(['setMenuState']),
    ...mapActions({
      register: 'auth/register',
      login: 'auth/login',
      logout: 'auth/logout',
      loginFacebook: "auth/loginFacebook",
      authInit: "auth/init"
    }),
    ...mapGetters({
      getUserPhoto: 'auth/getUserPhoto',
      isAdmin: 'auth/isAdmin',
      isEditor: 'auth/isEditor',
      canManage: 'auth/canManage',
      userGroup: 'auth/userGroup'
    })
  },
  mounted(){
    // this.authInit()
    $("input.loginPwd").keypress((ev)=> {
          var keycode = (ev.keyCode ? ev.keyCode : ev.which);
          if (keycode == '13') {
            this.login(this.loginData)
          }
      })
  }
}



